@mixin lg {
  @include r(1900) {
    @content; } }

@mixin md {
  @include r(1199) {
    @content; } }

@mixin sm {
  @include r(1023) {
    @content; } }

@mixin xs {
  @include r(767) {
    @content; } }

@mixin mob-fix {
  @media only screen and (max-width: 767px) and (orientation: landscape) {
    @content; } }

@mixin trs {
  transition: all 0.25s ease-out; }

// titles
h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: inherit;
  -webkit-margin-before: 0;
  -webkit-margin-after: 0; }

.is-hidden {
  display: none; }

.desk-only {
  @include md {
    display: none; } }
