h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: inherit;
  -webkit-margin-before: 0;
  -webkit-margin-after: 0;
}

.is-hidden {
  display: none;
}

@media only screen and (max-width: 1199px) {
  .desk-only {
    display: none;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes rotating {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

button {
  background-color: transparent;
}

a:hover, a:focus {
  text-decoration: none;
}

a {
  color: inherit;
  text-decoration: none;
}

table {
  vertical-align: top;
}

img {
  max-width: 100%;
  vertical-align: top;
}

* {
  -webkit-tap-highlight-color: transparent;
}

input {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.icon-arr {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-like {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-search {
  width: 1em;
  height: 1em;
  fill: initial;
}

body {
  position: relative;
  min-width: 320px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "GothamPro", Arial, sans-serif;
  font-weight: normal;
  font-size: 18px;
  line-height: 28px;
  color: #666666;
}

@media only screen and (max-width: 767px) {
  body {
    font-size: 16px;
    line-height: 26px;
  }
}

body.is-locked {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 0;
  overflow: hidden;
}

.container {
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 15px;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -15px;
}

.icon-search {
  fill: green;
}

.nav {
  max-width: 40%;
  flex: 0 0 40%;
}

.nav__list {
  display: flex;
  align-items: center;
}

.nav__list-item {
  padding-right: 20px;
}

.nav__list-item.has-dropdown {
  position: relative;
}

.nav__list-link {
  display: flex;
  align-items: center;
}

.nav__list-link svg {
  height: 10px;
  margin-left: 3px;
}

.header {
  top: 0;
  position: fixed;
  left: 0;
  width: 100%;
  background: #fff;
}

.header__content {
  padding: 26px 0px;
  position: relative;
}

.header__container {
  display: flex;
  align-items: center;
}

.header__lang {
  position: relative;
  margin: 0 15px;
  z-index: 2;
  width: 80px;
  text-transform: uppercase;
}

.header__lang ul {
  position: relative;
}

.header__lang ul:hover ul {
  opacity: 1;
  pointer-events: auto;
  transform: scaleY(1);
}

.header__lang ul li a {
  display: flex;
  align-items: center;
  padding: 0 15px;
  font-size: 15px;
  letter-spacing: 1.5px;
  transition: all 0.25s ease-out;
}

.header__lang ul li .icon-arr {
  margin-left: 4px;
}

.header__lang ul ul {
  position: absolute;
  top: calc(100% - 33px);
  left: 0;
  opacity: 0;
  pointer-events: none;
  transform: scaleY(0);
  transform-origin: 30% 0;
  padding: 33px 0 10px;
  width: 100%;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.11);
  cursor: pointer;
  transition: .3s ease;
}

.header__logo {
  max-width: 20%;
  flex: 0  0 20%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 15px;
}

.header__options {
  display: flex;
  max-width: 40%;
  flex: 0 0 40%;
  justify-content: flex-end;
  align-items: center;
}

.header__search {
  padding: 0 15px;
}

.header__search a {
  display: flex;
}

.header__search svg {
  fill: #000;
  height: 19px;
}

.header__like {
  display: flex;
  padding: 0 15px;
  align-items: center;
}

.header__like a {
  display: flex;
}

.header__cart {
  position: relative;
  padding: 0 15px;
}

.header__cart-count {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  text-align: center;
  font-size: 12px;
  z-index: 1;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  position: absolute;
  right: -4px;
  top: -4px;
  background: #000;
}

.cart {
  position: relative;
  border: 1px solid red;
}

.cart__count::before {
  content: '';
  display: block;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  position: absolute;
  left: 0;
  top: 0;
  background: #000;
}
