button {
	background-color: transparent; }

a:hover, a:focus {
	text-decoration: none; }

a {
	color: inherit;
	text-decoration: none; }

table {
	vertical-align: top; }

img {
	max-width: 100%;
	vertical-align: top; }

* {
	-webkit-tap-highlight-color: transparent; }

input {
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none; }
